.container {
    width: 50%;
    padding: 0.2em;
  }

.video {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    border: 1px solid lightgray;
    box-shadow: 3px 3px 5px 0px rgba(191, 191, 191, 0.53);
    cursor: pointer;
    transition: transform 250ms ease-in;
}

.metadata {
    width: 60%;
    margin-left: 0.5em;
}

  
.title {
    margin:0;
}

.thumbnail {
    width: 40%;
    height: 100%;
  }
  